import type { EventEmitter } from '@stencil/core';
import { Component, Element, Event, h, Method, Prop, State, Watch, Host } from '@stencil/core';
import type { IconName } from '@fortawesome/fontawesome-svg-core';

const toastStack = Object.assign(document.createElement('div'), {
	// ToDo: When we expose root.css we should replace the
	// hardcoded properties with css variables.
	// position: var(--o-toast-stack-position);
	// top: var(--o-toast-stack-top);
	// right: var(--o-toast-stack-right);
	// z-index: var(--o-toast-stack-z-index);
	style: `
        position: fixed;
        top: 20px;
        right: 20px;
        z-index: 9999;
    `
});

@Component({
	tag: 'o-toast',
	styleUrl: 'o-toast.scss',
	shadow: true
})
export class OToast {
	private autoHideTimeout?: number;

	@Element() host!: HTMLOToastElement;

	@State() isVisible = false;
	@State() isExiting = false;
	@State() isEntering = false;

	/** Indicates whether or not the toast is open. */
	@Prop({ mutable: true, reflect: true }) open = false;

	/** Set to true to make the toast closable. */
	@Prop({ reflect: true }) closable = false;

	/** The variant of the toast. */
	@Prop({ mutable: true, reflect: true }) variant: 'primary' | 'success' | 'neutral' | 'warning' | 'danger' = 'primary';

	/** The length of time, in milliseconds, the toast will show before closing itself. */
	@Prop({ mutable: true, reflect: true }) duration = Infinity;

	/** Emitted when the toast opens. */
	@Event() oShow!: EventEmitter<void>;

	/** Emitted when the toast closes. */
	@Event() oHide!: EventEmitter<void>;

	@Watch('open')
	handleOpenChange() {
		this.open ? this.show() : this.hide();
	}

	@Watch('duration')
	handleDurationChange() {
		this.restartAutoHide();
	}

	componentWillLoad() {
		if (this.open) {
			this.show();
		}
	}

	/** Shows the toast. */
	@Method()
	async show() {
		if (this.isVisible) {
			return;
		}

		// Check if the host is connected to the DOM
		if (this.host.isConnected) {
			const event = this.oShow.emit();
			if (event.defaultPrevented) {
				this.open = false;
				return;
			}
		}
		this.isEntering = true;
		this.isVisible = true;
		this.open = true;

		/* istanbul ignore next */
		// Perform DOM manipulation if not in a testing environment
		if (typeof jest === 'undefined') {
			// Append the toastStack to the body if it's not already there
			if (!toastStack.parentElement) {
				document.body.append(toastStack);
			}

			// Append the current toast to the toastStack
			toastStack.append(this.host);
		}
		/* istanbul ignore next */
		// RequestAnimationFrame for entering animation in non-test environments
		if (typeof requestAnimationFrame !== 'undefined') {
			requestAnimationFrame(() => {
				this.isEntering = false;
			});
		} else {
			// Fallback for testing
			this.isEntering = false;
		}

		if (this.duration < Infinity) {
			this.autoHideTimeout = window.setTimeout(() => this.hide(), this.duration);
		}
	}

	/** Hides the toast. */
	@Method()
	async hide() {
		if (!this.isVisible) {
			return;
		}

		const event = this.oHide.emit();
		if (event.defaultPrevented) {
			this.open = true;
			return;
		}

		clearTimeout(this.autoHideTimeout);
		this.isExiting = true;
		this.isVisible = false;
		this.open = false;
		/* istanbul ignore next */
		// Perform DOM manipulation if not in a testing environment
		if (typeof jest === 'undefined') {
			// Remove the toast from the stack after a delay
			await new Promise((resolve) => setTimeout(resolve, 300));
			this.isVisible = false;
			this.open = false;
			this.isExiting = false;
			this.host.remove();
			if (toastStack.childElementCount === 0) {
				toastStack.remove();
			}
		}
	}

	/** Displays the toast notification. */
	@Method()
	async toast() {
		return new Promise<void>((resolve) => {
			/* istanbul ignore next */
			if (typeof jest === 'undefined') {
				if (!toastStack.parentElement) {
					document.body.append(toastStack);
				}
				toastStack.append(this.host);
			}

			requestAnimationFrame(() => this.show());

			this.host.addEventListener(
				'o-toast-hide',
				() => {
					resolve();
				},
				{ once: true }
			);
		});
	}

	/** Pauses the autoHide functionality. */
	private pauseAutoHide() {
		clearTimeout(this.autoHideTimeout);
	}

	/** Resets the autoHide timer. */
	private resetAutoHide() {
		if (this.open && this.duration < Infinity) {
			this.autoHideTimeout = window.setTimeout(() => this.hide(), this.duration);
		}
	}

	/** Restarts the autoHide timer. */
	private restartAutoHide() {
		this.pauseAutoHide();
		this.resetAutoHide();
	}

	private handleCloseClick = () => {
		this.hide();
	};

	private handleMouseEnter = () => {
		this.pauseAutoHide();
	};

	private handleMouseLeave = () => {
		this.resetAutoHide();
	};

	/* istanbul ignore next */
	private handleTransitionEnd = (event: TransitionEvent) => {
		const target = event.target as HTMLElement;
		if (event.propertyName === 'opacity' && target.classList.contains('toast')) {
			this.isVisible = this.open;
		}
	};

	private getIconDetails() {
		switch (this.variant) {
			case 'success':
				return { iconName: 'circle-check' as IconName, iconColor: 'spring_green' };
			case 'warning':
				return { iconName: 'circle-exclamation' as IconName, iconColor: 'expire_orange' };
			case 'danger':
				return { iconName: 'circle-xmark' as IconName, iconColor: 'flame_red' };
			case 'neutral':
				return { iconName: 'circle-info' as IconName, iconColor: 'white' };
			case 'primary':
			default:
				return { iconName: 'circle-info' as IconName, iconColor: 'accent_blue_1' };
		}
	}

	private getVariantClasses() {
		switch (this.variant) {
			case 'success':
				return 'border-spring_green';
			case 'warning':
				return 'border-expire_orange';
			case 'danger':
				return 'border-flame_red';
			case 'neutral':
				return 'border-white';
			case 'primary':
			default:
				return 'border-accent_blue_1';
		}
	}

	render() {
		// Return null if the toast is not visible
		if (!this.isVisible) {
			return null;
		}

		const { iconName, iconColor } = this.getIconDetails();

		const baseClass =
			'flex items-center transition-opacity duration-300 ease-in-out border-t-4 rounded-lg shadow-lg bg-header_blue bg-opacity-90 text-white w-[330px] xl:w-[440px] px-[25px] py-[17px] mb-3.5';
		const stateClass = this.isExiting
			? 'opacity-0 -translate-y-4'
			: this.isEntering
				? 'opacity-0 translate-y-4'
				: 'opacity-100 translate-y-0';
		const variantClass = this.getVariantClasses();

		return (
			<Host>
				<div
					class={`${baseClass} ${stateClass} ${variantClass}`}
					role="alert"
					aria-live="assertive"
					aria-atomic="true"
					aria-hidden={!this.open}
					onTransitionEnd={this.handleTransitionEnd}
					onMouseEnter={this.handleMouseEnter}
					onMouseLeave={this.handleMouseLeave}
				>
					<o-icon name={iconName} color={iconColor} size="md" class="pr-7"></o-icon>
					<div class="text-sm flex-1 overflow-hidden">
						<slot />
					</div>

					{this.closable && (
						<div class="relative top-[-10px]">
							<o-icon-button
								name="xmark"
								size="sm"
								color="white"
								onClick={this.handleCloseClick}
								label="Close"
								class="scale-[1.4]"
							></o-icon-button>
						</div>
					)}
				</div>
			</Host>
		);
	}
}
